.theme-card {
  flex-basis: 33.33333%;
  padding: 0 15px; }
  .theme-card .theme-card-inner {
    border: 1px solid #e6e6e6;
    border-radius: 5px;
    overflow: hidden;
    transition: box-shadow ease 0.5s;
    height: 100%; }
    .theme-card .theme-card-inner > a {
      color: inherit; }
      .theme-card .theme-card-inner > a:hover {
        text-decoration: none; }
    .theme-card .theme-card-inner:hover, .theme-card .theme-card-inner.selected {
      -webkit-box-shadow: 0px 4px 5px 0px #b0b0b0;
      -moz-box-shadow: 0px 4px 5px 0px #b0b0b0;
      box-shadow: 0px 4px 5px 0px #b0b0b0; }
  .theme-card .theme-thumb img {
    width: 100%;
    height: auto; }
  .theme-card .theme-info {
    padding: 15px; }
    .theme-card .theme-info .theme-name {
      font-weight: bold;
      font-size: 16px; }
    .theme-card .theme-info .style-amount {
      margin: 10px 0;
      color: grey;
      font-weight: bold; }
