.single-attach-file .attachments-list-wrap {
  padding: 15px;
  margin-bottom: 15px;
  border: 1px solid #cecece; }
  .single-attach-file .attachments-list-wrap.empty {
    padding: 0;
    border: none;
    margin-bottom: 0; }
  .single-attach-file .attachments-list-wrap.error {
    border-color: red; }
  .single-attach-file .attachments-list-wrap .attachment-item {
    max-width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    position: relative;
    padding-right: 15px; }
  .single-attach-file .attachments-list-wrap .file-remove-btn {
    position: absolute;
    right: 0;
    top: 0;
    color: red;
    cursor: pointer; }

.single-attach-file .attach-visual-link {
  position: relative; }
  .single-attach-file .attach-visual-link > a {
    position: relative;
    z-index: 10;
    display: block;
    width: 180px;
    height: 26px; }
  .single-attach-file .attach-visual-link [name="emptyfile"] {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 11;
    opacity: 0;
    cursor: pointer; }
  .single-attach-file .attach-visual-link [name="filetoupload"] {
    display: none; }
