.step-container.step-3 h3 {
  margin-bottom: 15px; }

.step-container.step-3 .options-wrapper .Polaris-ChoiceList__Choices li {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee; }
  .step-container.step-3 .options-wrapper .Polaris-ChoiceList__Choices li:last-child {
    border-bottom: none; }

.step-container.step-3 .options-wrapper .Polaris-ChoiceList__Choices .import-loading {
  display: inline-block;
  margin-left: 5px;
  position: relative; }
  .step-container.step-3 .options-wrapper .Polaris-ChoiceList__Choices .import-loading.loading {
    top: -5px; }
  .step-container.step-3 .options-wrapper .Polaris-ChoiceList__Choices .import-loading.success i {
    color: green; }
  .step-container.step-3 .options-wrapper .Polaris-ChoiceList__Choices .import-loading.false i {
    color: red; }
  .step-container.step-3 .options-wrapper .Polaris-ChoiceList__Choices .import-loading.partial i {
    color: orange; }

.step-container.step-3 .sync-btn {
  text-align: center;
  margin-top: 15px; }
  .step-container.step-3 .sync-btn i {
    margin-right: 5px; }
