.code-list-detail {
  margin-bottom: 20px; }
  .code-list-detail .Polaris-ResourceList-Item {
    background: #fff;
    cursor: default;
    border-radius: 3px;
    box-shadow: 0 0 0 1px rgba(63, 63, 68, 0.05); }
    .code-list-detail .Polaris-ResourceList-Item:hover {
      background-image: none; }
  .code-list-detail .item-container {
    padding: 10px 0;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start; }
    .code-list-detail .item-container > .Polaris-Icon {
      margin-top: 0; }
    .code-list-detail .item-container .item-col {
      flex-grow: 1;
      padding: 0 15px; }
      .code-list-detail .item-container .item-col.item-col-1 {
        flex-basis: 40%; }
      .code-list-detail .item-container .item-col.item-col-2, .code-list-detail .item-container .item-col.item-col-3 {
        flex-basis: 30%; }
      .code-list-detail .item-container .item-col .template-name {
        font-weight: bold;
        margin-bottom: 10px; }
      .code-list-detail .item-container .item-col .hidden-code {
        color: #3f4eae;
        font-weight: bold; }
      .code-list-detail .item-container .item-col .support-status span {
        font-weight: bold;
        text-transform: capitalize; }
        .code-list-detail .item-container .item-col .support-status span.available {
          color: green; }
        .code-list-detail .item-container .item-col .support-status span.expired {
          color: red; }
    .code-list-detail .item-container .item-col-3 {
      text-align: right; }
      .code-list-detail .item-container .item-col-3 .Polaris-Button {
        clear: both;
        float: right;
        min-width: 147px;
        margin-bottom: 10px; }
        .code-list-detail .item-container .item-col-3 .Polaris-Button:last-child {
          margin-bottom: 0; }
