.support-form {
  max-width: 800px;
  margin: 0 auto; }
  .support-form .DraftEditor-editorContainer {
    min-height: 150px; }
  .support-form label {
    width: 140px;
    font-size: 16px; }
    .support-form label.required:after {
      content: '*';
      color: red;
      display: inline-block;
      margin-left: 1px; }
  .support-form .error-msg {
    color: red; }
    .support-form .error-msg .Polaris-Icon {
      float: left;
      margin-right: 5px; }
  .support-form .controls {
    width: calc(100% - 140px);
    padding-left: 15px; }
    .support-form .controls.error > div {
      border-color: red; }
