.perk-list {
  margin-left: -15px;
  margin-right: -15px; }
  .perk-list .perk-list-wrapper {
    display: flex;
    flex-wrap: wrap; }
  .perk-list .perk-item {
    padding: 0 15px;
    margin-bottom: 30px;
    flex-basis: 25%; }
    .perk-list .perk-item .image-wrapper {
      position: relative; }
    .perk-list .perk-item .claim-btn-wrapper .Polaris-Button {
      padding: 12px 35px;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      visibility: hidden;
      opacity: 0;
      transition: all ease 0.5s; }
    .perk-list .perk-item .claim-btn-wrapper .Polaris-Button__Content {
      font-size: 15px;
      white-space: nowrap; }
    .perk-list .perk-item .perk-logo {
      display: block;
      margin: 0 auto; }
    .perk-list .perk-item .perk-name {
      font-weight: bold;
      color: #3f4eae;
      margin-top: 10px;
      margin-bottom: 5px; }
    .perk-list .perk-item .perk-deal {
      font-weight: bold;
      color: #000;
      text-transform: capitalize;
      font-size: 18px;
      margin-bottom: 10px; }
    .perk-list .perk-item .perk-desc {
      margin-bottom: 30px; }
    .perk-list .perk-item .perk-status {
      font-weight: bold;
      color: #bbb;
      position: absolute;
      left: 15px;
      bottom: 15px; }
  .perk-list .perk-item-wrapper {
    padding: 15px;
    height: 100%;
    background: #fff;
    border-radius: 5px;
    border: 1px solid #eee;
    position: relative;
    cursor: pointer;
    transition: all ease 0.5s; }
    .perk-list .perk-item-wrapper:hover {
      box-shadow: 0px 4px 5px 0px #b0b0b0; }
      .perk-list .perk-item-wrapper:hover .Polaris-Button {
        visibility: visible;
        opacity: 1; }
